.h1 {
    /* background: linear-gradient(90deg, #74ebd5 0%, #9face6 100%) !important; */
    font-family: 'BioRhyme', 'Fascinate', cursive, Inline;
    background: -webkit-linear-gradient(120deg, #74ebd5, #9face6);
    /* background: linear-gradient(90deg, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%); */
    /* background-image: linear-gradient(180deg, #fff, #87f1ff); */
    font-weight: bolder !important;
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
    /* filter: drop-shadow(2px 2px #0085a3); */
    /* filter: drop-shadow(3px 3px black); */
    text-shadow: 1px 1px 2px #74ebd5, 0 0 1em blue, 0 0 0.2em blue;
    /* text-shadow: 1px 1px 2px #74ebd5, 0 0 1em pink, 0 0 0.2em pink; */
    color: black;
    /* font-size: 70px; */
    text-align: center;
}

